.formSearch {
  position: relative;
}

@media screen and (min-width: 320px) {
  .inputSearch {
    width: 300px;
    height: 49px;
    border: 2px solid #dddddd;
    box-sizing: border-box;
    border-radius: 10px;

    /* font-family: Gotham Pro; */
    font-style: normal;
    font-weight: normal;
    font-size: 17px;
    line-height: 120%;
    color: #5c5c5c;
    padding-left: 7px;
  }
}

@media screen and (min-width: 1530px) {
  .inputSearch {
    width: 450px;
  }
}

@media screen and (min-width: 1730px) {
  .inputSearch {
    width: 653px;
  }
}

.buttonSearch {
  position: absolute;
  right: 11px;
  top: 11px;
  border: none;
  background-color: #fff;
}
